* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Gilroy", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  height: 100%;
  min-width: 320px;
  min-height: 100vh;
  font-size: 12px;
  line-height: 1;
  color: #000000; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1;
  min-height: 100vh; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input {
  -webkit-appearance: none; }

textarea {
  -webkit-appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.scroll-lock {
  min-height: 100vh;
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 140px;
  margin: 0 auto; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 40px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 20px; } }

.btn {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
  transition: opacity 0.2s ease;
  cursor: pointer; }

.section-title {
  font-size: 20px;
  font-weight: normal; }

.section-subtitle {
  font-size: 18px;
  line-height: 140%;
  max-width: 621px;
  margin: 0;
  text-align: center; }

.header {
  position: absolute;
  left: 0;
  top: 0; }
  .header__wrap {
    display: flex;
    flex-direction: column; }
  .header__btn {
    width: 131px;
    height: 113px;
    background-color: #393D46;
    background-image: url("../img/burger.svg");
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center; }
  .header__nav-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 54px 34px 34px;
    min-height: 877px;
    background-color: #F6F7FB; }
  .header__nav-item {
    color: #7987A5;
    margin-bottom: 33px; }
    .header__nav-item.active {
      color: #1C295D; }
      .header__nav-item.active .header__nav-icon {
        background: #FF6647;
        box-shadow: 0px 10px 20px rgba(88, 92, 229, 0.25); }
    .header__nav-item:last-of-type {
      margin-bottom: 0;
      margin-top: auto; }
  .header__nav-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: opacity 0.2s ease; }
    .header__nav-link:hover {
      opacity: 0.7; }
  .header__nav-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    margin-bottom: 16px;
    background-color: #FFFFFF;
    border-radius: 10px; }

.intro {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #F6F7FB;
  padding: 22px 428px 30px 175px; }
  .intro__title {
    font-size: 26px;
    font-weight: 600; }
  .intro__search {
    display: flex;
    align-items: center;
    position: relative; }
    .intro__search::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      left: 14px;
      top: 15px;
      background-image: url("../img/icon-search.svg");
      background-repeat: no-repeat;
      background-size: auto;
      background-position: center; }
  .intro__input {
    width: 326px;
    height: 52px;
    padding: 0 47px;
    background: #FFFFFF;
    box-shadow: 0px 24px 24px rgba(0, 0, 0, 0.02);
    border-radius: 26px;
    border: 1px solid transparent;
    font-size: 15px;
    color: #AFAFAF;
    transition: border-color 0.2s ease; }
    .intro__input:hover {
      border-color: #7987A5; }
  .intro__btn {
    width: 52px;
    height: 52px;
    margin-left: 10px;
    background-color: #F86549;
    background-image: url("../img/tune.svg");
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
    border-radius: 50%;
    transition: opacity 0.2s ease; }
    .intro__btn:hover {
      opacity: 0.7; }

.sidebar {
  position: absolute;
  right: 0;
  top: 0;
  font-family: "Sofia Pro", Arial, Helvetica, sans-serif;
  font-size: 16px;
  width: 397px;
  padding: 20px 18px;
  background: rgba(255, 255, 255, 0.9);
  color: #757575; }
  .sidebar__title {
    color: #000000;
    font-size: 20px;
    line-height: 31px;
    font-weight: normal;
    margin: 0; }
  .sidebar__main-image {
